import React, { useState, useMemo, useEffect } from 'react';
import { Ingredients } from '../Ingredients/Ingredients.jsx';
import { useSelector } from 'react-redux';
import { Tab } from '@ya.praktikum/react-developer-burger-ui-components';
import styles from './BurgerIngredients.module.css';
import { useInView } from 'react-intersection-observer';

export const BurgerIngredients = () => {
  const [current, setCurrent] = useState('')
  const [bunRef, inViewBun] = useInView({threshold: .1});
  const [sauceRef, inViewSauce] = useInView({threshold: .1});
  const [mainRef, inViewMain] = useInView({threshold: .5});
  const ingredients = useSelector(state => state.ingredientsReducer.ingredients);

  const buns = useMemo(() => ingredients.filter(item => item.type === 'bun'), [ingredients]);
  const sauces = useMemo(() => ingredients.filter(item => item.type === 'sauce'), [ingredients]);
  const mains = useMemo(() => ingredients.filter(item => item.type === 'main'), [ingredients]);

  useEffect(() => {
    if (inViewBun) {
      setCurrent('');
    } else if (inViewSauce) {
      setCurrent('');
    } else if (inViewMain) {
      setCurrent('');
    }
  }, [inViewBun, inViewSauce, inViewMain]);

  return (
    <section className={`${styles.content} mr-10`}>
      <h1 className="text text_type_main-large mt-10 mb-5"> </h1>
      <div className={styles.tabs}>
        <Tab value="" active={current === ''} onClick={setCurrent}>
        
        </Tab>
        <Tab value="" active={current === ''} onClick={setCurrent}>
        
        </Tab>
        <Tab value="" active={current === ''} onClick={setCurrent}>
        
        </Tab>
      </div>
      <ul className={`${styles.menu} mt-10`}>
        <li ref={bunRef}>
          <Ingredients data={buns} title={''} />
        </li>
        <li ref={sauceRef} >
          <Ingredients data={sauces} title={''} />
        </li>
        <li ref={mainRef}>
          <Ingredients data={mains} title={''} />
        </li>
      </ul>
    </section>
  );
}

